'use client'
import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchUser } from "../features/userSlice";
import { RootState, AppDispatch } from "../store/store";
import { useState } from 'react';
import Select from './select';
import { useTexts } from '../context/Texts';
import Image from "next/image";
import FamilyImg from "../icons/FamilyImg";

const options = [
  { value: "dni", label: "DNI" },
  { value: "carnet", label: "CE" },
  { value: "pasaporte", label: "Pasaporte" },
];

const ContactForm: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();
  const { data, loading, error } = useSelector((state: RootState) => state.user);
  
  const [formData, setFormData] = useState(data);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const { t } = useTexts();

  useEffect(() => {
    dispatch(fetchUser());
  }, [dispatch]);

  const handleSelectChange = (value: string) => {
    console.log("Selected:", value);
  };

  const handleChange = (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = event.target;

   // setFormData((prevFormData) => ({ ...prevFormData, [name]: value }));
  };

  const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
    console.log(data, formData, 'datosssss')

    event.preventDefault();
    setIsSubmitting(true);
    setTimeout(() => {
      setIsSubmitting(false);
      setIsSubmitted(true);
    }, 2000);
  };

  return (
    <div className="max-w-md mx-auto divide-y sm:divide-y-0 flex flex-col sm:flex-row gap-6">
      <div className="flex justify-center items-center gap-4 sm:flex-col-reverse ">
        <div>
          <p className='text-[12px] text-center font-bold w-[140px] bg-custom-gradient'>{t.form.caption}</p>
          <h2 className="text-2xl font-bold mb-4">{t.form.title}</h2>
        </div>
        <Image
          src="/Family.svg"
          alt="familia"
          width={136}
          height={160}
         
        />
      </div>
      <form onSubmit={handleSubmit} className='pt-6'>
        <h3 className='mb-4 text-sm'>{t.form.subtitle}</h3>
        <div className="mb-4 grid grid-cols-2 border border-gray-300 rounded-lg divide-x">
          <Select options={options} onChange={handleSelectChange}></Select>
          <input
            className='w-full p-2 rounded-r-lg'
            type="number"
            id="id"
            name="id"
            placeholder='N. de identificacion'
            //value={formData.id}
            onChange={handleChange}
          />
        </div>
        <div className="mb-4">
          <input
            className="w-full p-2 border border-gray-300 rounded-lg focus:outline-none focus:ring focus:border-blue-500"
            type="number"
            id="phone"
            name="phone"
            placeholder='Celular'
           // value={formData.phone}
            onChange={handleChange}
          />
        </div>
        <div className="flex flex-col gap-3">
          <div className='flex'>
            <input type='checkbox' />
            <p className="text-gray-600 text-xs">{t.form.privacy}</p>
          </div>
          <div className='flex'>
            <input type='checkbox' />
            <p className="text-gray-600 text-xs">{t.form.trades}</p>
          </div>
          <a href="#" className=" underline text-xs">{t.form.terms}</a>
        </div>
        <button
          className={`my-12 w-full p-2 bg-black text-white rounded-lg focus:outline-none focus:ring focus:border-blue-500 ${isSubmitting ? 'opacity-50 cursor-not-allowed' : ''}`}
          type="submit"
          disabled={isSubmitting}
        >
          {isSubmitting ? t.form.submitted : t.form.submit}
        </button>
        {isSubmitted && (
          <p className="text-green-500 mt-4">{t.form.message}</p>
        )}
      </form>
    </div>
  );
};

export default ContactForm;
